<template>
  <div>
    <span v-if="linkContent">{{ `sent ${linkContent} to ${platform}` }}</span>
  </div>
</template>

<script>
export default {
  name: "Access",
  data() {
    return {
      platform: "",
      linkContent: "",
    };
  },
  created() {
    const userAgent = navigator.userAgent.toLowerCase();
    if (/iphone|ipad|ipod/.test(userAgent)) {
      // iOS
      this.platform = "IOS";
    } else if (/android/.test(userAgent)) {
      // Android
      this.platform = "Android";
    } else {
      this.platform = "Web";
    }
  },
  mounted() {
    this.linkContent = this.$getCookie("link");
    if (!this.linkContent) {
      this.linkContent = "no cookie";
    }
    if (this.platform === "IOS") {
      if (window.webkit) {
        window.webkit.messageHandler.getAppLink.postMessage(this.linkContent);
      }
    }
    if (this.platform === "Android") {
      if (window.Android) {
        window.Android.getAppLink(this.linkContent);
      }
    }
  },
};
</script>
